body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html{--bg-one: #000000;--bg-two: #333e6e;--bg-three: #0e9ca7;--bg-panel: #fff;--color-headings: #fff;--color-text: #333;--color-fun: #c44394}body{color:#fff;color:var(--color-headings);background:radial-gradient(black 15%, transparent 16%) 0 0,radial-gradient(black 15%, transparent 16%) 8px 8px,radial-gradient(rgba(255,255,255,0.1) 15%, transparent 20%) 0 1px,radial-gradient(rgba(255,255,255,0.1) 15%, transparent 20%) 8px 9px;background-color:#282828;background-size:16px 16px;font-family:'Muli', sans-serif;min-height:100vh;display:grid;place-items:center;text-align:center}.link-right{position:absolute;right:16px;top:16px}.link-right .switch-logo{-webkit-filter:drop-shadow(0px 0px 16px white);filter:drop-shadow(0px 0px 16px white);height:48px}h1{font-size:75px;margin:10px;font-family:'Rowdies', sans-serif;text-shadow:2px 2px 3px rgba(0,0,0,0.3)}img.logo{width:400px;position:absolute;margin-left:-200px;margin-top:-110px}.countdown-div{width:580px;margin:auto;min-height:70vh}@media screen and (max-width: 500px){.countdown-div{width:inherit}.countdown-div .countdown-wrapper{padding-top:80px}}.countdown-wrapper{margin-top:200px;display:flex;min-height:200px;align-items:center;justify-content:center;flex-wrap:wrap;background:linear-gradient(315deg, white 0%, #a4a5a5 74%);border-radius:10px;box-shadow:4px 4px 12px 2px rgba(0,0,0,0.3);padding:40px 40px 30px}.countdown-item{color:#333;color:var(--color-text);font-size:4rem;display:flex;align-items:center;justify-content:center;flex-direction:column;line-height:30px;margin:10px;padding-top:10px;position:relative;width:100px;height:100px}@media screen and (max-width: 1366px){.countdown-item{font-size:3rem}}.countdown-item span{color:#333;color:var(--color-text);font-size:1rem;padding-top:1rem;text-transform:uppercase}.countdown-svg{position:absolute;top:0;left:0;width:100px;height:100px}.arrow{margin-top:120px}

h3.vertical-timeline-element-title{text-transform:uppercase;margin:8px 0}h3.vertical-timeline-element-title a{color:white;text-decoration:none}h4.vertical-timeline-element-subtitle{margin:0;color:white;background:#3d3d3d;border-radius:25px;padding:4px 18px}h4.vertical-timeline-element-subtitle a{color:white;text-decoration:none}.vertical-timeline-element-description{display:block;-webkit-margin-before:1em;margin-block-start:1em;-webkit-margin-after:1em;margin-block-end:1em;-webkit-margin-start:0px;margin-inline-start:0px;-webkit-margin-end:0px;margin-inline-end:0px}.event-logo{width:inherit}h1 a.schedule-link,h3 a.schedule-link{color:white;text-decoration:none}.channel-wrapper{display:inline-block;margin:16px}.channel-wrapper .channel-logo{height:76px;border-radius:16px;-webkit-filter:grayscale(0.6);filter:grayscale(0.6)}.channel-wrapper .channel-logo:hover{-webkit-filter:grayscale(0);filter:grayscale(0)}

.currently-running{margin-top:64px}.currently-running.crisis{margin-top:16px}.currently-running .title{text-transform:uppercase;margin:8px 0}.currently-running .subtitle{margin:0;color:white;background:#3d3d3d;border-radius:25px;padding:4px 18px}.currently-running .subtitle a{text-decoration:none;color:white}.currently-running .timing{color:#2e2e2e}.currently-running a{text-decoration:none;color:white}.currently-running .status{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;text-align:center;background:#c47a43;margin:16px auto 16px auto;padding:4px 32px;font-size:16px;border-radius:32px;-webkit-animation:hue-rotate 3s linear infinite;animation:hue-rotate 3s linear infinite}.currently-running .status.live{background:#c44343;-webkit-filter:drop-shadow(2px 2px 6px #c44343);filter:drop-shadow(2px 2px 6px #c44343)}.currently-running .status:hover{-webkit-filter:grayscale(0.2);filter:grayscale(0.2)}@-webkit-keyframes hue-rotate{0%{-webkit-filter:grayscale(0);filter:grayscale(0)}50%{-webkit-filter:grayscale(0.6);filter:grayscale(0.6)}100%{-webkit-filter:grayscale(0);filter:grayscale(0)}}@keyframes hue-rotate{0%{-webkit-filter:grayscale(0);filter:grayscale(0)}50%{-webkit-filter:grayscale(0.6);filter:grayscale(0.6)}100%{-webkit-filter:grayscale(0);filter:grayscale(0)}}.streaming-disabled{pointer-events:none;cursor:default}

